.AddSupplier>form {
    width: 250px;
    border: 1px solid;
    text-align: left;
    margin: 20px auto;
    padding: 20px;
    box-shadow: 10px 10px 10px;
    border-radius: 10px;
    font-family: monospace;
    font-weight: bold;

}

.AddSupplier input,
.AddSupplier button {
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 20px;
}